import React from "react";
import { Item } from '../../types/item'
import { FilterType } from '../../utils/FilterType';
import { Loader } from '../Loader/Loader'

type Props = {
    items: Item[];
    sortBy: (filter: FilterType) => void;
    query: string;
    setQuery: (variable: string) => void;
    isLoading: boolean;
    setSelectedItem: (variable: Item) => void;
};

export const ItemsList: React.FC<Props> = ({ items, sortBy, query, setQuery, isLoading, setSelectedItem}) => {
    return (
    <>
        <div className="inputField">
            <input
                className="input is-normal input2" 
                type="text"
                placeholder="Search"
                value={query}
                onChange={(event) => {
                    const { value } = event.target;

                    setQuery(value);
                }}
            />
        </div>
        {isLoading === true
            ? <Loader />
            : (
            <table
                className="table is-striped is-hoverable is-narrow"
            >
            <thead>
                <tr>
                    <th>
                        <button 
                            className="button is-white"
                            onClick={() => sortBy(FilterType.Name)}
                        >
                            Name
                        </button>
                    </th>
                    <th>
                        <button 
                            className="button is-white"
                            onClick={() => sortBy(FilterType.Date)}
                        >
                            Date
                        </button>
                    </th>
                    <th>
                        <button 
                            className="button is-white"
                            onClick={() => sortBy(FilterType.State)}
                        >
                            State
                        </button>
                    </th>
                </tr>
            </thead>

            <tbody>
                {items.map(item => (
                <tr
                    key={item._id}
                    onClick={() => setSelectedItem(item)}
                >
                    <td>{item.name}</td>
                    <td>{String(item.date)
                        .slice(0, 10)
                        .replace('-', '.')
                        .replace('-', '.')} 
                    </td>
                    {item.isActive === true 
                        ? (<td style={{"color": "green"}}>Active</td>)
                        : (<td style={{"color": "red"}}>Disable</td>)}
                </tr>
                ))}
            </tbody>
            </table>
        )}
    </>
    )
}